.offers{
  &-item + &-item{
    margin-top: 40px;
  }
  &__input{
    width: 100%;

    height: 60px;

    background: #0A2545;
    border-radius: 14px;

    display: flex;
    justify-content: space-between;
    padding-right: 15px;
    input{
      font-weight: 500;
      font-size: 18px;
      color:#fff;

      width: 100%;
      height: 100%;

      border: none;
      outline: none;
      background: none;

      padding: 20px;

      &::placeholder{
        opacity: 1;
        letter-spacing: -0.3px;
        color: #14386E;
      }
    }
  }
  &__select{
    font-size: 18px;
    font-weight: 500;
    color:#ffff;
    background: none;
    outline: none;
    border: none;
  }

  &__wrapper{
    max-width: 530px;
    width: 100%;

    margin: 0 auto;
  }

  &__footer{
    margin-top: 18px;
  }


  &__arrow{
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 15px 0;
  }

  &__content{
    margin-top: 40px;
  }

  &__exchange{
    text-align: center;

    color: #297BC7;

    font-size: 22px;
  }

  &__btn{
    width: 100%;
    height: 60px;
  }
}