*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Medium/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Regular/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Bold/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold/Roboto-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto',sans-serif;
}

.btn {
  -webkit-appearance: none;
  outline: none;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 64px;
  font-size: 24px;
  font-weight: 700;
  letter-spacing: -0.3px;
  color: #FFFFFF;
}

.btn:hover {
  cursor: pointer;
}

.btn-yellow {
  background: linear-gradient(111.04deg, #FFD900 -19.61%, #E86F00 94.09%);
  box-shadow: 0px 32px 20px -20px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.btn-blue {
  background: linear-gradient(111.04deg, #32C5FF -19.61%, #1E5BA4 94.09%);
  box-shadow: 0px 32px 20px -20px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.btn-disabled {
  background: #C4C4C4;
}

.page-wrapper__content {
  display: flex;
  align-items: stretch;
  min-height: calc(100vh - 70px);
}

.page-content {
  width: calc(100% - 320px);
  overflow: hidden;
}

.page-content__wrapper {
  background-color: #002D58;
  width: 100%;
  height: 100%;
  padding: 48px 33px;
}

.page-block__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-block__title {
  font-size: 44px;
  font-weight: 700;
  color: #FFFFFF;
}

.page-block__content {
  margin-top: 44px;
}

.currency-select {
  background: none;
  outline: none;
  border: none;
  font-size: 34px;
  font-weight: 500;
  color: #fff;
}

.trigger.active .trigger-block {
  display: block;
}

.trigger-block {
  display: none;
}

.trigger-link:hover {
  cursor: pointer;
}

.language-select {
  background: none;
  outline: none;
  border: none;
  text-transform: uppercase;
  color: #fff;
  font-weight: 500;
  letter-spacing: -0.3px;
  font-size: 14px;
}

.radio-input__label {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.radio-input__label:hover {
  cursor: pointer;
}

.radio-input__item {
  display: none;
}

.radio-input__item:checked ~ .radio-input__checkmark {
  background-color: #66FF66;
}

.radio-input__item:checked ~ .radio-input__checkmark::before {
  transform: translateX(24px);
}

.radio-input__text {
  font-size: 18px;
  font-weight: 500;
  letter-spacing: -0.3px;
  color: #FFFFFF;
}

.radio-input__checkmark {
  position: relative;
  width: 60px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: #297BC7;
  border-radius: 30px;
  padding: 2px;
  transition: .3s all;
}

.radio-input__checkmark::before {
  position: absolute;
  content: '';
  width: 32px;
  height: 32px;
  display: block;
  background-color: #Fff;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.15), 0px 3px 1px rgba(0, 0, 0, 0.06);
  border-radius: 50%;
  transition: 0.3s all linear;
  left: 2px;
  top: 2px;
}

.back-arrow {
  display: inline-flex;
  transform: rotate(180deg);
  margin-right: 20px;
}

.sort-item {
  display: flex;
}

.sort-item__icon {
  width: 18px;
  height: 18px;
  margin-right: 12px;
}

.sort-item__icon svg {
  width: 100%;
  height: 100%;
}

.sort-item__label {
  letter-spacing: -0.3px;
  color: #329DFF;
  font-size: 18px;
}

@media screen and (max-width: 991px) {
  .page-content {
    width: 100%;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #14386E;
  filter: drop-shadow(0px 4px 12px rgba(0, 0, 0, 0.15));
  padding: 0 48px 0 33px;
  height: 70px;
}

.header__block {
  display: flex;
  align-items: center;
}

.header__menu {
  margin-right: 30px;
}

.header__notification {
  margin-left: 30px;
}

.header__profile {
  margin-left: 45px;
}

.header__languages {
  margin-left: 40px;
}

.header-notification__counter {
  position: absolute;
  top: -8px;
  right: -12px;
}

.burger {
  width: 22px;
  height: 22px;
  transition: .3s all linear;
}

.burger svg {
  width: 100%;
  height: 100%;
}

.burger.hide {
  transform: scale(-1, 1) translateX(5px);
}

.notification, .refresh {
  width: 22px;
  height: 22px;
}

.notification svg, .refresh svg {
  width: 100%;
  height: 100%;
}

.notification {
  position: relative;
}

.notification-counter {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #FF9933;
  font-size: 12px;
  color: #fff;
  font-weight: 700;
}

.profile__wrapper {
  display: flex;
  align-items: center;
}

.profile__link {
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.3px;
  color: #FFFFFF;
}

.profile__img {
  position: relative;
  margin-left: 14px;
}

.profile__img-item {
  width: 34px;
  height: 34px;
  border-radius: 50%;
}

.profile__img-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile__img-verify {
  position: absolute;
  top: -3px;
  right: -7px;
}

.left-aside {
  background-color: #14386E;
  flex-shrink: 0;
}

.left-aside__wrapper {
  padding: 60px 0;
}

.page-list {
  width: 320px;
  transition: .3s all;
}

.page-list.hide {
  width: 80px;
}

.page-list.hide .page-list__item {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-list.hide .page-list__link-text, .page-list.hide .page-list__link-wrapper::after {
  display: none;
}

.page-list__item {
  padding: 0 34px;
}

.page-list__item_current .page-list__link-icon path {
  fill: #fff;
}

.page-list__item_current .page-list__link-text {
  color: #fff;
}

.page-list__item + .page-list__item {
  margin-top: 40px;
}

.page-list__link-wrapper {
  display: flex;
  align-items: center;
  position: relative;
}

.page-list__link-wrapper::after {
  content: '';
  width: 16px;
  height: 16px;
  display: block;
  background: url("../img/white-arrow.svg") center center no-repeat;
  background-size: contain;
  margin-left: auto;
}

.page-list__link-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
}

.page-list__link-icon svg {
  width: 100%;
  height: 100%;
}

.page-list__link-text {
  margin-left: 17px;
  font-weight: 500;
  font-size: 18px;
  color: #329DFF;
}

@media screen and (max-width: 991px) {
  .left-aside {
    position: fixed;
    left: 0;
    top: 70px;
    height: 100vh;
    opacity: 0;
    z-index: -1;
  }
}

.login-page__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  background-color: #002D58;
  min-height: 100vh;
  padding: 100px 0 100px 0;
}

.login-page__img {
  height: 215px;
  margin-bottom: 55px;
}

.login-page__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.login-page__sign-in {
  width: 300px;
}

.login-page__back {
  width: 200px;
  margin: 0 auto;
}

.login-page__resend {
  margin: 60px auto;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  color: #BEBEBE;
}

.login-page__content {
  padding: 0 15px;
  max-width: 460px;
  width: 100%;
}

.login-page__footer {
  margin-top: 100px;
}

.login-page__link {
  margin-top: 40px;
  display: block;
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.3px;
  text-decoration-line: underline;
  color: #329DFF;
}

.login-form__input + .login-form__input {
  margin-top: 20px;
}

.login-form__link {
  position: absolute;
  top: 50%;
  margin-top: -12px;
  right: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: -0.3px;
  text-decoration-line: underline;
  color: #329DFF;
}

.login-form__input {
  position: relative;
}

.login-form__input-item {
  height: 64px;
  width: 100%;
  padding: 20px;
  background: #114179;
  border: 2px solid #297BC7;
  border-radius: 10px;
  font-size: 20px;
  letter-spacing: -0.3px;
  color: #FFFFFF;
  outline: none;
}

.login-form__input-item::placeholder {
  opacity: 1;
}

.login-form__input-forgot {
  text-align: center;
  font-size: 48px;
}

.login-form__submit {
  margin-top: 55px;
}

.login-form__content {
  max-width: 200px;
  width: 100%;
  margin: 0 auto;
}

.login-form__text {
  color: #fff;
  font-weight: 500;
  font-size: 22px;
  text-align: center;
  margin-bottom: 60px;
}

.resend-code__time {
  font-weight: 500;
}

.resend-code__link {
  margin-left: 20px;
  text-decoration-line: underline;
  font-weight: 400;
  pointer-events: none;
}

.resend-code.active .resend-code__link {
  color: #32C5FF;
  pointer-events: auto;
}

.wallet-block + .wallet-block {
  margin-top: 44px;
}

.wallet-block__header {
  display: flex;
  align-items: center;
}

.wallet-block__title {
  color: #fff;
  font-size: 30px;
  font-weight: 500;
}

.wallet-block__select {
  margin-left: 50px;
  background: none;
  border: none;
  outline: none;
  font-size: 30px;
  font-weight: 500;
  color: #297BC7;
}

.wallet-block__help {
  margin-top: 15px;
}

.wallet-block__content {
  margin-top: 40px;
}

.wallet-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-auto-rows: 260px;
  gap: 30px;
}

.wallet-card_empty {
  grid-column-end: span 4;
  grid-row-end: span 1;
}

.wallet-card__wrapper {
  border: 2px solid #1E5BA4;
  border-radius: 16px;
  height: 100%;
  overflow: hidden;
  transition: .3s border-color;
}

.wallet-card__wrapper:hover {
  border-color: #fff;
}

.wallet-card__content {
  padding: 32px 26px 28px 26px;
  display: flex;
  align-items: center;
}

.wallet-card__info {
  width: 100%;
}

.wallet-card__img {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-right: 20px;
}

.wallet-card__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.wallet-card__header {
  display: flex;
  align-items: center;
}

.wallet-card__name {
  font-size: 16px;
  color: #fff;
}

.wallet-card__name span {
  font-weight: 700;
}

.wallet-card__address {
  display: flex;
  align-items: center;
  margin-left: 16px;
}

.wallet-card__address-select {
  background: none;
  border: none;
  outline: none;
  letter-spacing: -0.3px;
  color: #297BC7;
  font-size: 16px;
}

.wallet-card__address-control {
  display: flex;
  align-items: center;
}

.wallet-card__address-link {
  width: 20px;
  height: 20px;
  display: block;
}

.wallet-card__address-link svg {
  width: 100%;
  height: 100%;
}

.wallet-card__address-link {
  margin-left: 10px;
}

.wallet-card__remove {
  margin-left: auto;
}

.wallet-card__remove-icon svg path {
  transition: .3s all;
}

.wallet-card__remove-icon:hover svg path {
  fill: #FF3333;
  fill-opacity: 1;
}

.wallet-card__count-link {
  display: flex;
  align-items: center;
  font-size: 34px;
  font-weight: 500;
  letter-spacing: -0.3px;
  color: #fff;
  margin: 8px 0 12px 0;
}

.wallet-card__count-link::after {
  content: '';
  width: 16px;
  height: 16px;
  display: block;
  margin-left: 10px;
  background: url("../img/white-arrow.svg") center center no-repeat;
  background-size: contain;
}

.wallet-card__exchange {
  display: flex;
  align-items: center;
}

.wallet-card__exchange-all {
  font-size: 18px;
  color: #fff;
  letter-spacing: -.3px;
}

.wallet-card__exchange-one {
  margin: 0 24px;
  display: block;
  font-size: 18px;
  letter-spacing: -0.3px;
  color: #297BC7;
}

.wallet-card__footer {
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  gap: 30px;
  padding: 24px;
  background: #0A2545;
  border-radius: 0px 0px 14px 14px;
  margin-top: auto;
}

.wallet-card__btn {
  font-size: 18px;
  font-weight: 700;
  text-transform: capitalize;
  height: 50px;
}

.wallet-card__btn_big {
  grid-column-end: span 2;
}

.wallet-card__btn-icon {
  display: none;
  width: 30px;
  height: 30px;
}

.wallet-card__btn-icon svg {
  width: 100%;
  height: 100%;
}

.wallet-empty {
  height: 100%;
}

.wallet-empty__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.wallet-empty__title {
  margin-top: 25px;
  font-weight: 500;
  font-size: 22px;
  color: #fff;
}

.wallet-empty__text {
  margin-top: 10px;
  font-size: 16px;
  color: #fff;
}

.trend__wrapper {
  display: flex;
  align-items: center;
}

.trend__label {
  font-size: 18px;
  letter-spacing: -0.3px;
  color: #66FF66;
  margin-left: 6px;
}

.trend_decrise .trend__label {
  color: #FF3333;
}

.trend_decrise .trend__icon {
  transform: rotate(180deg);
}

.trend_decrise .trend__icon svg path {
  fill: #FF3333;
}

.help-block {
  position: relative;
}

.help-block__wrapper {
  display: flex;
  align-items: center;
  width: fit-content;
}

.help-block__wrapper:hover {
  cursor: pointer;
}

.help-block__wrapper:hover .help-block__icon path {
  fill: #fff;
}

.help-block__content {
  position: absolute;
  top: 30px;
  left: -50px;
  max-width: 510px;
  width: 100%;
  padding: 19px 21px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
}

.help-block__content::after {
  content: '';
  position: absolute;
  left: 50%;
  top: -20px;
  margin-left: -10px;
  border: 10px solid transparent;
  border-bottom: 10px solid rgba(0, 0, 0, 0.7);
}

.help-block__text {
  font-size: 16px;
  line-height: 22px;
  color: #fff;
}

.help-block__icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-left: 22px;
}

.help-block__icon svg {
  width: 100%;
  height: 100%;
}

.help-block__title {
  font-size: 18px;
  color: #297BC7;
}

@media screen and (max-width: 1660px) {
  .wallet-grid {
    grid-template-columns: minmax(0, 1fr);
  }
}

@media screen and (max-width: 767px) {
  .wallet-grid {
    grid-auto-rows: auto;
  }
  .wallet-card__wrapper {
    min-height: 250px;
  }
  .wallet-card__header {
    flex-direction: column;
    align-items: flex-start;
    position: relative;
  }
  .wallet-card__address {
    margin-left: 0;
    margin-top: 5px;
    flex-wrap: wrap;
  }
  .wallet-card__remove {
    position: absolute;
    top: 0;
    right: 0;
  }
  .wallet-card__exchange {
    flex-wrap: wrap;
  }
  .wallet-card__btn-text {
    display: none;
  }
  .wallet-card__btn-icon {
    display: block;
  }
}

@media screen and (max-width: 450px) {
  .wallet-card__content {
    flex-direction: column;
  }
  .wallet-card__img {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.settings-grid {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.settings-grid__item + .settings-grid__item {
  margin-left: 30px;
}

.settings-grid__item {
  max-width: 490px;
  width: 100%;
  flex-shrink: 0;
}

.settings-tabs__content {
  margin-top: 40px;
}

.settings-content__header {
  margin-bottom: 40px;
}

.settings-wrapper__item {
  padding: 50px 30px 66px 30px;
  border: 2px solid #1E5BA4;
  border-radius: 16px;
}

.settings-wrapper__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.settings-wrapper__title-label {
  font-size: 30px;
  font-weight: 500;
  line-height: 1.16;
  color: #fff;
}

.settings-wrapper__chat {
  margin-top: 40px;
}

.settings-wrapper__item + .settings-wrapper__item {
  margin-top: 30px;
}

.settings-wrapper__content {
  max-width: 1010px;
  width: 100%;
  margin: 0 auto;
}

.settings-wrapper__btn {
  height: 60px;
  margin-top: 60px;
}

.settings-list {
  display: flex;
  align-items: center;
}

.settings-list__item + .settings-list__item {
  margin-left: 40px;
}

.settings-list__item {
  display: flex;
  align-items: center;
}

.settings-list__item_current .settings-list__link {
  color: #fff;
}

.settings-list__link {
  font-weight: 500;
  font-size: 22px;
  letter-spacing: -0.3px;
  color: #329DFF;
}

.settings-list__link + .notification-counter {
  margin-left: 12px;
}

.new-link__label {
  font-size: 18px;
  letter-spacing: -0.3px;
  color: #329DFF;
}

.new-link__wrapper {
  display: flex;
  align-items: center;
}

.new-link__icon {
  width: 18px;
  height: 18px;
  display: block;
  margin-left: 15px;
}

.new-link__icon svg {
  width: 100%;
  height: 100%;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  z-index: -1;
  transition: .5s all;
}

.popup.active {
  opacity: 1;
  z-index: 99;
}

.popup-close:hover, .popup-trigger:hover {
  cursor: pointer;
}

.popup__content {
  margin-top: 60px;
}

.popup__wrapper {
  max-width: 600px;
  width: 100%;
  background: #14386E;
  box-shadow: 0px 4px 90px rgba(0, 0, 0, 0.15);
  border-radius: 14px;
  padding: 35px;
}

.popup__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.popup__title {
  font-size: 30px;
  font-weight: 700;
  color: #fff;
  display: flex;
